<template>
    <div class="card-wrap">
        <dndbox 
        v-for="(item,index) in mydata"
        :key="index+1"
        :index="index"
        :data="item"
        v-if="itemexist(item)"
        @mydrag="catchdragnum"
        @mydrop="catchdropnum"
        @dblclick.native="openDetail(index)">
        </dndbox>

    </div>
</template>
<script>

import { deepCopy } from '../../utils/assist.js'
import dndbox from './dndbox'
export default {
    name:'dndwrap',
    components:{

        dndbox
    },
    data(){
        return{
            isboxexist:true,
            dragnum:'',
            dropnum:'',
            currentMovingItem:0,
            envData:null,
        }
    },
    props:{
        data:[Object,Array],
        // currendEnvData:[Object,Array]  //以前的传出去的data的替代方案 现在不需要了
    },
    mounted(){
        this.envData=deepCopy(this.data)
    },
    methods:{
        
        catchdragnum(dragnum){
            //add的过程
            this.dragnum=dragnum
            
             
        },
        catchdropnum(dropnum){
           //remove的过程
           
            this.$nextTick(()=>{
                this.mydata[dropnum].cardinfo.children.push(this.mydata[this.dragnum]) 
                // let t = deepCopy(this.mydata[this.dragnum])
                this.mydata.splice(this.dragnum,1,null);
              


                // this.envData[dropnum].cardinfo.children.push(this.envData[this.dragnum])
                // this.envData.splice(this.dragnum,1,null);
                
            })
            
            this.dropnum=dropnum
            
        },
        openDetail(index){
            let openindex=index
           
            this.$emit('opendetail',this.mydata[this.dropnum],this.mydata[this.dragnum],openindex,this.envData)
        },
        
    
    },
    computed:{
        mydata:{
            get:function () {
                return this.data
            },
            set:function(){
                            //没有set会报警告 就添了个set
            },
            
        },
        itemexist(item){
            return function(item){
                return item?true:false //  被防止进别的文件夹之后，原来的文件位置上留下一个null来占位，以便我用一开始的index来获取drag和drop的对象 但是item为null在循环渲染中报错，所以用这个来规定哪些item需要渲染
            }
        }
        
    },
    watch:{
        
    }
}
</script>
<style scoped>
body .el-card__body{
    padding: 0px
}
.card-toolbar{
    background:#999;
    height: 49px;
}
.card-wrap{
    display: flex;
    flex-wrap:wrap;
}
.card-wrap .el-card {
    width:100px;
    -ms-flex-negative: 0;
    flex-shrink: 0;
    margin: 1% 1%;
    margin-bottom: 2%;
    min-height: 50px;
    border-radius: 10px;
}
.card-wrap .el-card .card-icon-wrapper i{
    font-size:60px;
  
}
.card-wrap .el-card .card-title-wrapper{
    padding:0 10px 5px 10px;
    cursor: default;
}


</style>


