<template>
    <div>
        <div class="card-icon-wrapper">
            <i :class="info.icon"></i>
            
        </div>
        <div class="card-title-wrapper">{{info.name}}</div>
    </div>
</template>

<script>
export default {
    name:'iconbox',
    props:{
        data:Object
    },
    computed:{
        info(){
            return this.data
        }
    }
}
</script>
<style scoped>
.card-icon-wrapper i{
    font-size:60px ;
  
}
.card-icon-wrapper{
    text-align: center;
}
.card-title-wrapper{
    margin:0 10px 5px 10px;
    cursor: default;
    height: 40px;
    text-overflow:ellipsis;
    overflow: hidden;
    text-align: center
}
</style>



