<template>
    <div>
        <div class="card-toolbar">
            <div>
                <v-breadcrumbs :items="navbtn">
                    <template slot="item" slot-scope="props">
                        <v-btn @click="handleclick(props)" round>{{ props.item }}</v-btn>
                    </template>
                </v-breadcrumbs>

            </div>
        </div>
        
        <dndwrap
        :data="currentData"
        @opendetail="openDetailDialog">
        </dndwrap>

        <v-btn @click="back"  block color="secondary" dark >后退</v-btn>
        
    </div>
</template>

<script>
import dndwrap from './dndwrap'
import { deepCopy,findTarget } from '../../utils/assist.js'
export default {
    name:'dnd',
    components:{
        dndwrap,
    },
    props:{
        data:[Object,Array]
    },
    data(){
        return{
            isshow:false,
            currentData:this.data,
            detailDialog:null,
            envdata:null,
            navbtn:[
                    '文件管理'
                ],
            activebtn:'文件管理',  //激活的按钮的名称
            testfunc:null //第一次打开文件夹的时候记录一下data 这样回退功能不需要从总的data里面开始找
        }
    },
    mounted(){
        this.envdata=deepCopy(this.data)  //原本传进来的数据 不会被操作改变
    },
    computed:{
        
    },
    methods:{
        openDetailDialog(dropval,dragval,openindex,envdata){
        
            this.isshow=true
            let openname=this.currentData[openindex].cardinfo.name;
            this.activebtn=openname;
            
            if(this.navbtn.indexOf(this.activebtn)<0){
               
                this.navbtn.push(openname);  //如果没有该数据，在面包屑中加入这一btn
            }
            if(this.navbtn.length==2){
                this.testfunc=this.currentData[openindex]       //第一个文件夹的data
               
            }

            //双击打开文件夹  及展示的是打开的文件夹下面的children
            this.currentData=this.currentData[openindex].cardinfo.children
           
            

        },
        back(){
            // this.isshow=false;

            let closeindex=this.navbtn.indexOf(this.activebtn)      //获取关闭的index
        
            if(closeindex>0){
                this.navbtn.splice(closeindex,1)        //当有这个name的时候再关闭它
                // this.activebtn = this.navbtn[closeindex-1] 
            }
            this.activebtn = this.navbtn[closeindex-1] 
            
            if(closeindex==1||closeindex==0){
                //等于按了第一个按钮
                this.navbtn=['文件管理'];// 清空其他的navbtn，回到最初的页面的状态
                this.activeindex=0;
                this.activebtn='文件管理'
                this.currentData=this.data
                this.testfunc=null;

                this.testfunc=null;
            }else{
                console.log(this.data)
           
                let t = findTarget([this.testfunc],this.activebtn)
                 
                this.currentData=t.cardinfo.children
            }


            
        },
        handleclick(prop){
            let index = this.navbtn.indexOf(prop.item)
           
            if(index==0){
                this.navbtn=['文件管理'];// 清空其他的navbtn，回到最初的页面的状态
                this.activeindex=0;
                this.activebtn='文件管理'
                this.currentData=this.data
                this.testfunc=null;
            }else{
                //激活其他btn的时候
         
                this.activebtn=this.navbtn[index]  //激活的按钮
    
                if(index!=this.navbtn.length){
                    this.navbtn.splice(index+1,this.navbtn.length-index-1)      //如果激活的按钮不是最后面的那个 则把激活按钮的后面的按钮都删除掉
              
                }
             

                //查到此文件夹
                // let t = findTarget(this.data,this.activebtn)
                // this.currentData=t.cardinfo.children
                this.handleactiveByName(this.activebtn)

            }
        },
        handleactiveByName(name){
            let t = findTarget([this.testfunc],name)
            this.currentData=t.cardinfo.children
        },
        
    }
}
</script>
<style scoped>
.card-toolbar{
    background:#999;
    min-height: 49px;
}

</style>


