function typeOf(obj) {
    const toString = Object.prototype.toString;
    const map = {
        '[object Boolean]'  : 'boolean',
        '[object Number]'   : 'number',
        '[object String]'   : 'string',
        '[object Function]' : 'function',
        '[object Array]'    : 'array',
        '[object Date]'     : 'date',
        '[object RegExp]'   : 'regExp',
        '[object Undefined]': 'undefined',
        '[object Null]'     : 'null',
        '[object Object]'   : 'object'
    };
    return map[toString.call(obj)];
}

// deepCopy
function deepCopy(data) {
    const t = typeOf(data);
    let o;

    if (t === 'array') {
        o = [];
    } else if ( t === 'object') {
        o = {};
    } else {
        return data;
    }

    if (t === 'array') {
        for (let i = 0; i < data.length; i++) {
            o.push(deepCopy(data[i]));
        }
    } else if ( t === 'object') {
        for (let i in data) {
            o[i] = deepCopy(data[i]);
        }
    }
    return o;
}

export {deepCopy};

//递归查询
function findTarget(source , targetName) {
    // source需要是数组形式 targetName是字符串形式
 
        
    let _result=null;
    for(let i=0;i<source.length;i++){
        // if(source[i].cardinfo.name==targetName) return source[i]
        // if(source[i].cardinfo.children) _result=findTarget(source[i].cardinfo.children, targetName)
        // if(_result != null) return _result
        if(source[i]!=null){

            if(_result ==null){
                if(source[i].cardinfo.name==targetName){
                    return source[i]
                }else if(source[i].cardinfo.children ){
                    _result=findTarget(source[i].cardinfo.children, targetName)
                }
            }
            else{
                return _result
            }
            

        }else{
            continue
        }
        // if(_result != null) return _result
    }

    return _result

}
  
export {findTarget}


